import React from 'react';
import { Button } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import ArrowBack from '@mui/icons-material/ArrowBack'
/**
 * The `UserManual` component provides a comprehensive guide for using the application.
 * It includes instructions for logging in, managing lists, sharing lists, and resolving common issues.
 * 
 * ### Features:
 * - Navigation back to the home page using a "Back" button.
 * - Structured user manual with sections for each core feature of the app.
 * 
 * @component
 * @returns {JSX.Element} A user manual with clear instructions and tips for using the application.
 */
const UserManual = () => {
  const navigate = useNavigate();

  /**
   * Handles the navigation to the previous page when the "Back" button is clicked.
   * 
   */
  const handleNavigateBack = () => {
    navigate('/');
  };

  return (
    <div style={{ padding: '20px' }}>
      <Button
        onClick={handleNavigateBack}
        style={{ marginTop: '20px' }}
      >
        <ArrowBack /> 
      </Button>
      <div style={{ textAlign: 'center', marginBottom: '20px' }}>
        <h1>Kyttjopas</h1>
      </div>
      
      <p>
        Tm opas auttaa sinua kyttmn sovellusta. Seuraamalla ohjeita voit hydynt kaikkia toimintoja ja saada kaiken irti sovelluksesta.
      </p>

      <h2>Sisnkirjautuminen</h2>
      <p>
        - Kirjaudu sisn kyttmll henkilkohtaista kyttjkoodiasi.<br />
        - Jos sinulla ei viel ole koodia, voit luoda uuden "Luo kyttjkoodi" -painikkeella.<br />
        - Laita kyttjkoodisi talteen, sill se on henkilkohtainen. <br />
        - Koodisi tallennetaan automaattisesti, joten sinun ei tarvitse kirjautua jokaisella kerralla uudellee, mikli kytt samaa laitetta.<br />
        - Huomioi kuitenkin, ett uloskirjautuminen poistaa kyttjistuntosi muistista. <br />
        - Yksityisen selauksen kytt edellytt kirjautumista jokaisella kerralla.
      </p>

      <h2>Listojen luominen</h2>
      <p>
        - Psivulla voit luoda uuden listan syttmll listan nimen ja painamalla "Luo lista" -painiketta.<br />
        - Voit antaa listallesi yksilllisen nimen, jonka avulla voit hallinnoida tehtvisi tehokkaammin. <br />
        - Jos poistat listan, joka on jaettu, poistuu se kaikilta vlittmsti. Listan merkitseminen suosikiksi tai sen piilottaminen ei vaikuta muihin kyttjiin.<br />
      </p>

      <h2>Listojen muokkaaminen</h2>
      <p>
        - Klikkaamalla listan nime pset tarkastelemaan sen sislt.<br />
        - Voit list, muokata tai poistaa kohteita listaltasi. Net mys kuinka monella on psy listalle Voit mys muokata listan nime.<br />
        - Kohteiden muokkaaminen on yksinkertaista ja nopeaa, joten tehtvsi pysyvt aina ajan tasalla.<br />
        - Mikli lista on jaettu ystviesi kesken, muokkauksen vaikutukset nkyvt kaikille vlittmsti.<br />
      </p>

      <h2>Listojen jakaminen</h2>
      <p>
        - Kyt listan koodia jakaaksesi listasi muiden kyttjien kanssa.<br />
        - Net luomasi listan koodin, kun avaat listan painamalla sen nime. Koodi on helppo mys kopioida leikepydlle. <br />
        - Vastaanottajat voivat liitty listalle syttmll sen koodin sovelluksessa.<br />
        - Tm mahdollistaa tiimityn ja listojen yhteiskytn helposti.<br />
      </p>

      <h2>Listojen hallinta</h2>
      <p>
        - Voit merkit listat suosikeiksi painamalla thden kuvaketta. Suosikkilistat nousevat automaattisesti ylimmksi.<br />
        - Jos haluat piilottaa listan, klikkaa silmkuvaketta. Piilotettu lista ei ny en pnkymss.<br />
        - Kun listan kytt tulee taas ajankohtaiseksi, on sen palauttaminen helppoa. Painat vain listan nimen perss olevaa silmn kuvaketta. <br /> 
        - Nit asetuksia voit muokata milloin tahansa.<br />
      </p>

      <h2>Kyttjtilin hallinta</h2>
      <p>
        - Kirjautuessasi ulos kyttjtilistsi, sinun on annettava koodi uudelleen seuraavalla kerralla.<br />
        - Kyttjkoodin palauttaminen ei ole mahdollista, joten laita koodinumerosi talteen. <br />
        - Mikli hukkaat oman kyttjkoodisi lopullisesti, uuden koodin luonti on helppoa. Unphdetun koodin palauttaminen ei ole mahdollista.<br />
      </p>

      <h2>Ongelmatilanteet</h2>
      <p>
        - Jos kohtaat ongelmia, varmista ensin, ett internet-yhteytesi toimii.<br />
        - Mikli ongelmat jatkuvat, yrit kirjautua ulos ja sisn uudelleen.<br />
      </p>

      <h2>Tietosuoja</h2>
      <p>
        - Kyttjkoodin luominen ei vaadi henkiltietoja tai salasanaa. Tmn vuoksi kyttjkoodin palauttaminen ei ole mahdollista.<br />
        - Ethn tallenna sovellukseen mitn yksiliv tietoa, henkiltietoja, pankkitunnuksia tai salasanoja.<br />
      </p>
    </div>
  );
};

export default UserManual;
